/* Toggle Switch - Checkbox styling 
https://www.w3schools.com/howto/howto_css_switch.asp */
/* The switch - the box around the slider */
.MediaPlayer_switch__3YgmV {
  position: relative;
  display: inline-block;
  width: 60px;
  height: 34px;
}

/* Hide default HTML checkbox */
.MediaPlayer_switch__3YgmV input {
  opacity: 0;
  width: 0;
  height: 0;
}

/* The slider */
.MediaPlayer_slider__2zorg {
  position: absolute;
  cursor: pointer;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background-color: #ccc;
  -webkit-transition: .4s;
  transition: .4s;
}

.MediaPlayer_slider__2zorg:before {
  position: absolute;
  content: "";
  height: 26px;
  width: 26px;
  left: 4px;
  bottom: 4px;
  background-color: white;
  -webkit-transition: .4s;
  transition: .4s;
}

input:checked + .MediaPlayer_slider__2zorg {
  background-color: #2196F3;
}

input:focus + .MediaPlayer_slider__2zorg {
  box-shadow: 0 0 1px #2196F3;
}

input:checked + .MediaPlayer_slider__2zorg:before {
  -webkit-transform: translateX(26px);
  transform: translateX(26px);
}


